import { Badge } from "@/components/ui/badge"
import { cn } from "@/lib/utils"

/**
 * @typedef {'PENDING'|'PAID'|'PREPARING'|'READY_FOR_DELIVERY'|'DELIVERING'|'COMPLETED'|'CANCELLED'|'FAILED'} DroneStatus
 */

const statusConfig = {
  PENDING: { label: "Ch xc nhn", className: "bg-muted text-muted-foreground" },
  PAID: { label: " thanh ton", className: "bg-blue-500 text-white" },
  PREPARING: { label: "ang chun b", className: "bg-warning text-warning-foreground" },
  READY_FOR_DELIVERY: { label: "Sn sng giao", className: "bg-secondary text-secondary-foreground" },
  DELIVERING: { label: "ang bay", className: "bg-primary text-primary-foreground animate-pulse" },
  COMPLETED: { label: " giao", className: "bg-success text-success-foreground" },
  CANCELLED: { label: " hy", className: "bg-destructive text-destructive-foreground" },
  FAILED: { label: "Tht bi", className: "bg-destructive text-destructive-foreground" },
}

export function DroneStatusBadge({ status }) {
  const config = statusConfig[status]
  return (
    <Badge className={cn("font-medium", config.className)} variant="secondary">
      {config.label}
    </Badge>
  )
}
