import { useState } from "react"
import { User, MapPin, Bell, Lock, LogOut } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Switch } from "@/components/ui/switch"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { useToast } from "@/hooks/use-toast"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"

export default function AccountPage() {
  const [notifications, setNotifications] = useState(true)
  const { toast } = useToast()

  const handleSaveProfile = () => {
    toast({
      title: " lu thay i",
      description: "Thng tin ti khon  c cp nht",
    })
  }

  const handleChangePassword = () => {
    toast({
      title: " i mt khu",
      description: "Mt khu ca bn  c thay i thnh cng",
    })
  }

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto px-4 py-6">
        <h1 className="mb-6 text-2xl font-bold text-foreground md:text-3xl">Ti khon ca ti</h1>

        <div className="grid gap-6 lg:grid-cols-4">
          {/* Profile Card */}
          <Card className="lg:col-span-1">
            <CardContent className="flex flex-col items-center p-6">
              <Avatar className="h-24 w-24">
                <AvatarFallback className="bg-gradient-to-br from-primary to-secondary text-2xl text-primary-foreground">
                  NV
                </AvatarFallback>
              </Avatar>
              <h3 className="mt-4 text-xl font-semibold text-foreground">Nguyn Vn A</h3>
              <p className="text-sm text-muted-foreground">nguyenvana@example.com</p>
              <Button variant="outline" className="mt-4 w-full bg-transparent">
                i nh i din
              </Button>
            </CardContent>
          </Card>

          {/* Settings Tabs */}
          <div className="lg:col-span-3">
            <Tabs defaultValue="profile" className="w-full">
              <TabsList className="w-full justify-start">
                <TabsTrigger value="profile" className="gap-2">
                  <User className="h-4 w-4" />
                  H s
                </TabsTrigger>
                <TabsTrigger value="addresses" className="gap-2">
                  <MapPin className="h-4 w-4" />
                  a ch
                </TabsTrigger>
                <TabsTrigger value="settings" className="gap-2">
                  <Bell className="h-4 w-4" />
                  Ci t
                </TabsTrigger>
                <TabsTrigger value="security" className="gap-2">
                  <Lock className="h-4 w-4" />
                  Bo mt
                </TabsTrigger>
              </TabsList>

              {/* Profile Tab */}
              <TabsContent value="profile" className="mt-6">
                <Card>
                  <CardHeader>
                    <CardTitle>Thng tin c nhn</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="grid gap-4 sm:grid-cols-2">
                      <div className="space-y-2">
                        <Label htmlFor="fullName">H v tn</Label>
                        <Input id="fullName" defaultValue="Nguyn Vn A" />
                      </div>
                      <div className="space-y-2">
                        <Label htmlFor="phone">S in thoi</Label>
                        <Input id="phone" defaultValue="0909 123 456" />
                      </div>
                      <div className="space-y-2 sm:col-span-2">
                        <Label htmlFor="email">Email</Label>
                        <Input id="email" type="email" defaultValue="nguyenvana@example.com" />
                      </div>
                    </div>
                    <Button onClick={handleSaveProfile}>Lu thay i</Button>
                  </CardContent>
                </Card>
              </TabsContent>

              {/* Addresses Tab */}
              <TabsContent value="addresses" className="mt-6">
                <Card>
                  <CardHeader>
                    <CardTitle>a ch giao hng</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="rounded-lg border border-border p-4">
                      <div className="mb-2 flex items-center justify-between">
                        <h4 className="font-medium text-foreground">a ch mc nh</h4>
                        <Button variant="ghost" size="sm">Sa</Button>
                      </div>
                      <p className="text-sm text-muted-foreground">12 Nguyn Hu, Qun 1, TP.HCM</p>
                      <p className="text-sm text-muted-foreground">ST: 0909 123 456</p>
                    </div>
                    <Button variant="outline" className="w-full bg-transparent">Thm a ch mi</Button>
                  </CardContent>
                </Card>
              </TabsContent>

              {/* Settings Tab */}
              <TabsContent value="settings" className="mt-6">
                <Card>
                  <CardHeader>
                    <CardTitle>Ci t thng bo</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="font-medium text-foreground">Thng bo n hng</p>
                        <p className="text-sm text-muted-foreground">Nhn thng bo v trng thi n hng</p>
                      </div>
                      <Switch checked={notifications} onCheckedChange={setNotifications} />
                    </div>
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="font-medium text-foreground">Khuyn mi</p>
                        <p className="text-sm text-muted-foreground">Nhn thng bo v u i v khuyn mi</p>
                      </div>
                      <Switch />
                    </div>
                    <div className="flex items-center justify-between">
                      <div>
                        <p className="font-medium text-foreground">Email marketing</p>
                        <p className="text-sm text-muted-foreground">Nhn email v mn n mi v s kin</p>
                      </div>
                      <Switch />
                    </div>
                  </CardContent>
                </Card>
              </TabsContent>

              {/* Security Tab */}
              <TabsContent value="security" className="mt-6">
                <Card>
                  <CardHeader>
                    <CardTitle>i mt khu</CardTitle>
                  </CardHeader>
                  <CardContent className="space-y-4">
                    <div className="space-y-2">
                      <Label htmlFor="currentPassword">Mt khu hin ti</Label>
                      <Input id="currentPassword" type="password" />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="newPassword">Mt khu mi</Label>
                      <Input id="newPassword" type="password" />
                    </div>
                    <div className="space-y-2">
                      <Label htmlFor="confirmNewPassword">Xc nhn mt khu mi</Label>
                      <Input id="confirmNewPassword" type="password" />
                    </div>
                    <Button onClick={handleChangePassword}>i mt khu</Button>
                  </CardContent>
                </Card>

                <Card className="mt-6">
                  <CardHeader>
                    <CardTitle className="text-destructive">Vng nguy him</CardTitle>
                  </CardHeader>
                  <CardContent>
                    <Button variant="destructive" className="gap-2">
                      <LogOut className="h-4 w-4" />
                      ng xut
                    </Button>
                  </CardContent>
                </Card>
              </TabsContent>
            </Tabs>
          </div>
        </div>
      </div>
    </div>
  )
}
