import { useState } from "react"
import { ChevronLeft, Star } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Textarea } from "@/components/ui/textarea"
import { useToast } from "@/hooks/use-toast"
import { useNavigate, useParams } from "react-router-dom"
import { Link } from "react-router-dom"
import { cn } from "@/lib/utils"

export default function ReviewPage() {
  const { orderId } = useParams()
  const [rating, setRating] = useState(0)
  const [hoveredRating, setHoveredRating] = useState(0)
  const [comment, setComment] = useState("")
  const { toast } = useToast()
  const navigate = useNavigate()

  const handleSubmit = (e) => {
    e.preventDefault()
    if (rating === 0) {
      toast({
        title: "Vui lng chn s sao",
        variant: "destructive",
      })
      return
    }

    toast({
      title: "Cm n nh gi ca bn!",
      description: "nh gi  c gi thnh cng",
    })
    navigate("/orders")
  }

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto px-4 py-6">
        <div className="mb-6 flex items-center gap-4">
          <Button variant="ghost" size="icon" asChild>
            <Link to="/orders">
              <ChevronLeft className="h-5 w-5" />
            </Link>
          </Button>
          <h1 className="text-2xl font-bold text-foreground">nh gi n hng</h1>
        </div>

        <div className="mx-auto max-w-2xl">
          <Card>
            <CardHeader>
              <CardTitle>Tri nghim ca bn th no?</CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Star Rating */}
                <div className="flex flex-col items-center gap-4">
                  <p className="text-sm text-muted-foreground">
                    Chn s sao
                  </p>
                  <div className="flex gap-2">
                    {[1, 2, 3, 4, 5].map((star) => (
                      <button
                        key={star}
                        type="button"
                        onClick={() => setRating(star)}
                        onMouseEnter={() => setHoveredRating(star)}
                        onMouseLeave={() => setHoveredRating(0)}
                        className="transition-transform hover:scale-110"
                      >
                        <Star
                          className={cn(
                            "h-12 w-12 transition-colors",
                            star <= (hoveredRating || rating) ? "fill-warning text-warning" : "text-muted-foreground",
                          )}
                        />
                      </button>
                    ))}
                  </div>
                  {rating > 0 && (
                    <p className="text-sm font-medium text-foreground">
                      {rating === 1 && "Rt t"}
                      {rating === 2 && "T"}
                      {rating === 3 && "Bnh thng"}
                      {rating === 4 && "Tt"}
                      {rating === 5 && "Tuyt vi"}
                    </p>
                  )}
                </div>

                {/* Comment */}
                <div className="mt-6">
                  <label htmlFor="comment" className="mb-2 block text-sm font-medium text-foreground">
                    Nhn xt ca bn
                  </label>
                  <Textarea
                    id="comment"
                    placeholder="Chia s tri nghim ca bn v mn n v dch v giao hng..."
                    value={comment}
                    onChange={(e) => setComment(e.target.value)}
                    rows={6}
                  />
                </div>

                <Button type="submit" className="w-full" size="lg">
                  Gi nh gi
                </Button>
              </form>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
