import { useState } from "react"
import { ChevronLeft } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { useToast } from "@/hooks/use-toast"
import { useNavigate, useParams } from "react-router-dom"
import { Link } from "react-router-dom"

// Mock users (restaurant owners)
const mockUsers = [
  { id: "1", name: "Nguyn Vn A", email: "nguyenvana@example.com" },
  { id: "2", name: "Trn Th B", email: "tranthib@example.com" },
  { id: "3", name: "L Vn C", email: "levanc@example.com" },
]

export default function CreateRestaurantPage() {
  const [isSubmitting, setIsSubmitting] = useState(false)
  const { toast } = useToast()
  const navigate = useNavigate()

  const handleSubmit = async (e) => {
    e.preventDefault()
    setIsSubmitting(true)

    // Simulate API call
    setTimeout(() => {
      setIsSubmitting(false)
      toast({
        title: "To nh hng thnh cng!",
        description: "Nh hng mi  c thm vo h thng",
      })
      navigate("/admin/restaurants")
    }, 1500)
  }

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto px-4 py-6">
        <div className="mb-6 flex items-center gap-4">
          <Button variant="ghost" size="icon" asChild>
            <Link to="/admin/restaurants">
              <ChevronLeft className="h-5 w-5" />
            </Link>
          </Button>
          <div>
            <h1 className="text-2xl font-bold text-foreground">Thm nh hng mi</h1>
            <p className="text-muted-foreground">To nh hng mi trn nn tng</p>
          </div>
        </div>

        <div className="mx-auto max-w-2xl">
          <Card>
            <CardHeader>
              <CardTitle>Thng tin nh hng</CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Owner Selection */}
                <div>
                  <Label htmlFor="owner">Ch s hu</Label>
                  <Select>
                    <SelectTrigger id="owner">
                      <SelectValue placeholder="Chn ch s hu" />
                    </SelectTrigger>
                    <SelectContent>
                      {mockUsers.map((user) => (
                        <SelectItem key={user.id} value={user.id}>
                          {user.name} ({user.email})
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                  <p className="mt-1 text-sm text-muted-foreground">Chn ngi dng s qun l nh hng ny</p>
                </div>

                {/* Restaurant Name */}
                <div>
                  <Label htmlFor="name">Tn nh hng</Label>
                  <Input id="name" placeholder="Fast Burger Drone" required />
                </div>

                {/* Phone */}
                <div>
                  <Label htmlFor="phone">S in thoi</Label>
                  <Input id="phone" type="tel" placeholder="0909 000 111" required />
                </div>

                {/* Address */}
                <div>
                  <Label htmlFor="address">a ch</Label>
                  <Input id="address" placeholder="123 Trn Hng o, Qun 1, TP.HCM" required />
                </div>

                {/* Description */}
                <div>
                  <Label htmlFor="description">M t</Label>
                  <Textarea id="description" placeholder="M t v nh hng..." rows={4} />
                </div>

                {/* Image URL */}
                <div>
                  <Label htmlFor="image">URL hnh nh</Label>
                  <Input id="image" placeholder="/restaurant-image.jpg" />
                  <p className="mt-1 text-sm text-muted-foreground">ng dn n hnh nh nh hng</p>
                </div>

                {/* Status */}
                <div>
                  <Label htmlFor="status">Trng thi</Label>
                  <Select defaultValue="APPROVED">
                    <SelectTrigger id="status">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="APPROVED"> duyt</SelectItem>
                      <SelectItem value="PENDING">Ch duyt</SelectItem>
                      <SelectItem value="SUSPENDED">Tm ngng</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <Button type="submit" className="w-full" size="lg" disabled={isSubmitting}>
                  {isSubmitting ? "ang to..." : "To nh hng"}
                </Button>
              </form>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
