import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Package, ChefHat, Plane, CheckCircle2, Star } from "lucide-react"
import { Link } from "react-router-dom"
import { Button } from "@/components/ui/button"

// Mock stats
const stats = [
  { title: "n hng mi", value: "12", icon: Package, color: "text-primary", bgColor: "bg-primary/10" },
  { title: "ang chun b", value: "8", icon: ChefHat, color: "text-warning", bgColor: "bg-warning/10" },
  { title: "ang giao", value: "15", icon: Plane, color: "text-secondary", bgColor: "bg-secondary/10" },
  { title: "Hon thnh hm nay", value: "47", icon: CheckCircle2, color: "text-success", bgColor: "bg-success/10" },
  { title: "nh gi trung bnh", value: "4.7", icon: Star, color: "text-warning", bgColor: "bg-warning/10" },
]

export default function OwnerDashboardPage() {
  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto px-4 py-6">
        <div className="mb-6 flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-foreground md:text-3xl">Bng iu khin</h1>
            <p className="text-muted-foreground">Qun l nh hng ca bn</p>
          </div>
          <Button asChild>
            <Link to="/owner/orders">Xem n hng</Link>
          </Button>
        </div>

        {/* Stats Grid */}
        <div className="mb-8 grid gap-6 sm:grid-cols-2 lg:grid-cols-5">
          {stats.map((stat) => {
            const Icon = stat.icon
            return (
              <Card key={stat.title}>
                <CardContent className="p-6">
                  <div className="flex items-center justify-between">
                    <div>
                      <p className="text-sm text-muted-foreground">{stat.title}</p>
                      <p className="mt-2 text-3xl font-bold text-foreground">{stat.value}</p>
                    </div>
                    <div className={`flex h-12 w-12 items-center justify-center rounded-xl ${stat.bgColor}`}>
                      <Icon className={`h-6 w-6 ${stat.color}`} />
                    </div>
                  </div>
                </CardContent>
              </Card>
            )
          })}
        </div>

        {/* Quick Actions */}
        <div className="grid gap-6 md:grid-cols-3">
          <Card className="transition-all">
            <CardHeader>
              <CardTitle>Qun l n hng</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="mb-4 text-sm text-muted-foreground">Xem v cp nht trng thi n hng</p>
              <Button className="w-full" asChild>
                <Link to="/owner/orders">Xem n hng</Link>
              </Button>
            </CardContent>
          </Card>

          <Card className="transition-all">
            <CardHeader>
              <CardTitle>Qun l thc n</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="mb-4 text-sm text-muted-foreground">Thm, sa, xa mn n trong thc n</p>
              <Button className="w-full" asChild>
                <Link to="/owner/menu">Qun l menu</Link>
              </Button>
            </CardContent>
          </Card>

          <Card className="transition-all">
            <CardHeader>
              <CardTitle>Qun l Drone</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="mb-4 text-sm text-muted-foreground">Theo di trng thi drone giao hng</p>
              <Button className="w-full" asChild>
                <Link to="/owner/drones">Xem drone</Link>
              </Button>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  )
}
