import { useState } from "react"
import { Plus, Pencil, Trash2 } from "lucide-react"
import { Button } from "@/components/ui/button"
import { Card, CardContent } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Switch } from "@/components/ui/switch"
import { useToast } from "@/hooks/use-toast"

// Mock menu items
const initialMenuItems = [
  {
    id: "1",
    name: "Burger B Ph Mai",
    description: "Burger b c 100% vi ph mai cheddar tan chy",
    price: 89000,
    image: "/burger-cheese.jpg",
  available: true,
    category: "Combo",
  },
  {
    id: "2",
    name: "G Rn Gin",
    description: "G rn gin tan vi cng thc b mt",
    price: 59000,
    image: "/fried-chicken.jpg",
  available: true,
    category: "Combo",
  },
  {
    id: "3",
    name: "Khoai Ty Chin",
    description: "Khoai ty chin gin rm, n km tng c",
    price: 29000,
    image: "/french-fries.jpg",
  available: true,
    category: "Combo",
  },
]

export default function OwnerMenuPage() {
  const [menuItems, setMenuItems] = useState(initialMenuItems)
  const [isAddDialogOpen, setIsAddDialogOpen] = useState(false)
  const { toast } = useToast()

  const handleToggleAvailability = (id) => {
    setMenuItems((prev) => prev.map((item) => (item.id === id ? { ...item, available: !item.available } : item)))
    toast({
      title: " cp nht trng thi",
    })
  }

  const handleDelete = (id) => {
    setMenuItems((prev) => prev.filter((item) => item.id !== id))
    toast({
      title: " xa mn n",
    })
  }

  return (
    <div className="min-h-screen bg-background">
      <div className="container mx-auto px-4 py-6">
        <div className="mb-6 flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-foreground md:text-3xl">Qun l thc n</h1>
            <p className="text-muted-foreground">Thm, sa, xa mn n</p>
          </div>
          <Dialog open={isAddDialogOpen} onOpenChange={setIsAddDialogOpen}>
            <DialogTrigger asChild>
              <Button className="gap-2">
                <Plus className="h-4 w-4" />
                Thm mn mi
              </Button>
            </DialogTrigger>
            <DialogContent className="max-w-2xl">
              <DialogHeader>
                <DialogTitle>Thm mn n mi</DialogTitle>
              </DialogHeader>
              <form className="space-y-4">
                <div className="space-y-2">
                  <Label htmlFor="name">Tn mn</Label>
                  <Input id="name" placeholder="Burger B Ph Mai" />
                </div>
                <div className="space-y-2">
                  <Label htmlFor="description">M t</Label>
                  <Textarea id="description" placeholder="M t chi tit v mn n" />
                </div>
                <div className="grid gap-4 sm:grid-cols-2">
                  <div className="space-y-2">
                    <Label htmlFor="price">Gi ()</Label>
                    <Input id="price" type="number" placeholder="89000" />
                  </div>
                  <div className="space-y-2">
                    <Label htmlFor="category">Danh mc</Label>
                    <Input id="category" placeholder="Combo" />
                  </div>
                </div>
                <div className="space-y-2">
                  <Label htmlFor="image">URL hnh nh</Label>
                  <Input id="image" placeholder="/burger-cheese.jpg" />
                </div>
                <Button type="submit" className="w-full">Thm mn</Button>
              </form>
            </DialogContent>
          </Dialog>
        </div>

        <div className="grid gap-4 sm:grid-cols-2 lg:grid-cols-3">
          {menuItems.map((item) => (
            <Card key={item.id} className="overflow-hidden">
              <div className="relative aspect-video">
                <img src={item.image || "/placeholder.svg"} alt={item.name} className="h-full w-full object-cover" />
                <Badge className="absolute right-2 top-2" variant={item.available ? "default" : "secondary"}>
                  {item.available ? "Cn hng" : "Ht hng"}
                </Badge>
              </div>
              <CardContent className="p-4">
                <h3 className="mb-1 text-lg font-semibold text-foreground">{item.name}</h3>
                <p className="mb-2 line-clamp-2 text-sm text-muted-foreground">{item.description}</p>
                <p className="mb-3 text-lg font-bold text-primary">{item.price.toLocaleString("vi-VN")}</p>

                <div className="flex items-center justify-between border-t border-border pt-3">
                  <div className="flex items-center gap-2">
                    <Switch checked={item.available} onCheckedChange={() => handleToggleAvailability(item.id)} />
                    <span className="text-sm text-muted-foreground">Cn hng</span>
                  </div>
                  <div className="flex gap-2">
                    <Button variant="ghost" size="icon">
                      <Pencil className="h-4 w-4" />
                    </Button>
                    <Button variant="ghost" size="icon" onClick={() => handleDelete(item.id)}>
                      <Trash2 className="h-4 w-4 text-destructive" />
                    </Button>
                  </div>
                </div>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
}
